:root {
  --font: "Abel", sans-serif;
}

html {
  font-size: 62.5%;
  height: auto;
  width: auto;
}

body {
  box-sizing: border-box;
  margin: 0;
  width: auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  background: var(--cultured-2);
}

.App {
